body {
  background-color: #333745;
  color: white;
}

.App {
  text-align: center;
  font-family: sans-serif;
}

.Header {
  background-color: #474f5f;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 30px;
  font-size: 15px;
  color: white;
}

.Header__title {

  display: inline-block;
  position: absolute;
  left: 25px;
  align-items: center;
  justify-content: center;
  color: white;
}

.Header__subtitle {
  display: inline-block;
  position: absolute;
  right:25px;
  align-items: center;
  justify-content: center;
  color: white;
}

.container {

  max-width: 90rem;
  margin: 1.6rem auto;
  height: 100%;
  background-color: #222630;

}

.container__placeholder{

  height: 200px;
  

}


.container__subtitle {

  background-color:  #5e6477;
  padding: 10px;

}

.container__form {

  padding: 0px 20px 0px 20px;
  display:inline-block;

}

.container__content {
  margin: 0 0 100px 0;
  padding: 70px 0 70px 0;
  align-items: center;
  justify-content: center;
}

.container__input {

  background: #474f5f;
  border: none;
  color: white;
  padding: 1px;
  flex-grow: 1;
  margin: 0px 0px 0px 10px;
}

.image-container{

  height: 100%;
  text-align: center;  /* align the inline(-block) elements horizontally */
  font: 0/0 a; 
  display: inline-block;
   

}

.image-container:before{

  content: ' ';
  vertical-align: middle;  /* vertical alignment of the inline element */
  
}

.image{

  display: inline-block;
  vertical-align: middle;  /* vertical alignment of the inline element */
  font: 16px/1 Arial sans-serif;        /* <-- reset the font property */
  padding: 20px 20px 20px 20px;
}



.Footer-Component{

  background-color: #1e2129;
  color: white;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 40px;
  font-size: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.App-link {
  color: #61dafb;
}

